.slider {
    display: grid;
    grid-template-columns: minmax(auto, 70px) auto minmax(auto, 1fr) auto minmax(auto, 70px);
    grid-column-gap: 30px;
    background-image: url(/Cyclone-react/static/media/slider-bg.9b05380b.png);
    background-size: cover;
    position: relative;
    overflow: hidden;
}

.slider-list {
    grid-column: 3/4;
    grid-row: 2/3;
    width: 100%;
    min-width: 130px;
    word-wrap: break-word;
}

.swiper-slide {
    width: 100%;
}

.slider-item-number {
    grid-column: 3;
    grid-row: 1;
    padding: 90px 0 25px;
    font-size: 20px;
    font-weight: bold;
}

.slider-item-number {
    justify-self: stretch;
    text-align: center;
}

.slider-number-prev {
    grid-column: 1;
    -ms-writing-mode: tb-lr;
        writing-mode: vertical-lr;
    transform: rotate(180deg);
    left: -100px;
}

.slider-number-next {
    grid-column: 5;
    -ms-writing-mode: tb-lr;
        writing-mode: vertical-lr;
    transform: rotate(180deg);
    right: -60px;
}

.slider-number-prev,
.slider-number-next {
    grid-row: 2/3;
    align-self: center;
    color: white;
    position: absolute;
    font-size: 150px;
    font-weight: bold;
    cursor: default;
}

.slider-prev,
.slider-next {
    position: relative;
    padding: 10px 12px;
    width: 45px;
    height: 45px;
    color: #fffc8f;
    font-weight: bold;
    font-size: 18px;
    border: 2px solid #fffc8f;
    border-radius: 50%;
    grid-row: 2;
    align-self: center;
    cursor: pointer;
}

.slider-prev::after,
.slider-next::after {
    content: "";
    height: 2px;
    width: 60px;
    background-color: #fffc8f;
    position: absolute;
}

.slider-prev:hover,
.slider-next:hover {
    border-color: #bfbe87;
}

.slider-prev:hover::after,
.slider-next:hover::after {
    background-color: #bfbe87;
}

.slider-prev {
    grid-column: 2;
}

.slider-prev::after {
    left: 35px;
    top: 20px;
}

.slider-prev:hover .slider-prev::after {
    background-color: #e9e8a9;
}

.slider-next {
    grid-column: 4;
}

.slider-next::after {
    right: 35px;
    top: 20px;
}

.event-read-more {
    grid-column: 3;
    grid-row: 4;
    align-self: center;
    justify-self: center;
    padding: 85px 0 60px;
}

.event-read-more:hover img {
    -webkit-filter: contrast(50%);
            filter: contrast(50%);
}

